.custom-select-wrapper {
  // border: 3px solid red;
  font-family: "DM Sans", sans-serif;
  position: relative;
  width: 100%;
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10+ and Edge */
  user-select: none; /* Standard syntax */
}

.custom-select {
  // border: 2px solid green;
  position: relative;
  display: flex;
  flex-direction: column;
  border-width: 0 2px 0 2px;
}

.custom-select__trigger {
  background: #ffffff;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 em;
  min-height: 4.3em;
  // line-height: 43em;
  cursor: pointer;

  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  
  & span{
    font-size: 1.4em;
    padding: 4px 1em;
    text-align: left;
  }
}

.custom-options {
  position: absolute;
  text-align: left;
  background: #ffffff;
  display: block;
  top: calc(100% + 1em) ;
  left: 0;
  right: 0;
  opacity: 1;
  z-index: 2;
  transition: all 0.5s;
  overflow-y: auto;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  max-height: 50em;
  
}

.custom-select.open .custom-options {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

.custom-option {
  position: relative;
  display: block;
  padding: 0 1em 0 1em;
  font-size: 1.4em;
  font-weight: 300;
  line-height: 1.5em;
  cursor: pointer;
  transition: all 0.5s;

  &:hover {
    cursor: pointer;
    background-color: #b2b2b2;
  }

  &.selected {
    color: #ffffff;
    background-color: #305c91;
  }
}

.arrow {
  position: absolute;
  right: 10px;
  height: 2em;
  width: 2em;
  max-height: 30vh;
  background-color: #3d200d;
  background: url('./select-appreance.svg');
